        @keyframes loadingtext-opacity {
            0%  {opacity: 0}
            20% {opacity: 0}
            50% {opacity: 1}
            100%{opacity: 0}
        }

        @-moz-keyframes loadingtext-opacity {
            0%  {opacity: 0}
            20% {opacity: 0}
            50% {opacity: 1}
            100%{opacity: 0}
        }

        @-webkit-keyframes loadingtext-opacity {
            0%  {opacity: 0}
            20% {opacity: 0}
            50% {opacity: 1}
            100%{opacity: 0}
        }

        @-o-keyframes loadingtext-opacity {
            0%  {opacity: 0}
            20% {opacity: 0}
            50% {opacity: 1}
            100%{opacity: 0}
        }
        .loadingcontainer,
        .loading {
            height: 100px;
            position: relative;
            width: 100px;
            border-radius: 100%;
        }


        .loadingcontainer { margin: 40px auto }

        .loading {
            border: 2px solid transparent;
            border-color: transparent #d3cdff transparent #d3cdff;
            -moz-animation: rotate-loading 1.5s linear 0s infinite normal;
            -moz-transform-origin: 50% 50%;
            -o-animation: rotate-loading 1.5s linear 0s infinite normal;
            -o-transform-origin: 50% 50%;
            -webkit-animation: rotate-loading 1.5s linear 0s infinite normal;
            -webkit-transform-origin: 50% 50%;
            animation: rotate-loading 1.5s linear 0s infinite normal;
            transform-origin: 50% 50%;
        }

        .loadingcontainer:hover .loading {
            border-color: transparent #E45635 transparent #E45635;
        }
        .loadingcontainer:hover .loading,
        .loadingcontainer .loading {
            -webkit-transition: all 0.5s ease-in-out;
            -moz-transition: all 0.5s ease-in-out;
            -ms-transition: all 0.5s ease-in-out;
            -o-transition: all 0.5s ease-in-out;
            transition: all 0.5s ease-in-out;
        }

        #loadingtext {
            -moz-animation: loadingtext-opacity 2s linear 0s infinite normal;
            -o-animation: loadingtext-opacity 2s linear 0s infinite normal;
            -webkit-animation: loadingtext-opacity 2s linear 0s infinite normal;
            animation: loadingtext-opacity 2s linear 0s infinite normal;
            color: #000000;
            font-family: "Helvetica Neue, "Helvetica", ""arial";
            font-size: 12px;
            font-weight: bold;
            margin-top: 45px;
            opacity: 0;
            position: absolute;
            text-align: center;
            text-transform: uppercase;
            top: 0;
            width: 100px;
        }